<template>
  <div class="bookshelf">
    <van-nav-bar class="border" title="书架" left-text="返回" left-arrow @click-left="onClickLeft" />
    <div v-if="addBookData.length != 0">
      <div class="bookshelf-item clearfix" v-for="(item,index) in addBookData" :key="index">
        <div @click="bookDetail(item)">
          <div class="bookshelf-img fl">
            <img class="auto-img" :src="item.cover" alt />
          </div>
          <div class="bookshelf-detail fl">
            <div class="clearfix">
              <div class="bookshelf-title one-text fl">{{item.title}}</div>
            </div>
            <div class="bookshelf-author one-text">作者：{{item.author}}</div>
            <div class="bookshelf-shortIntro one-text">简介：{{item.longIntro}}</div>
            <div class="bookshelf-lastChapter one-text">更新至：{{item.lastChapter}}</div>
          </div>
        </div>
        <div class="delete fr" @click="del(index)">
          <van-icon name="delete" size="24px" />
        </div>
      </div>
    </div>
    <div v-else>
      <van-empty description="哎呀，你的书架空空如也，快去逛逛吧！" />
    </div>
  </div>
</template>
<script>
import { createNamespacedHelpers, mapGetters } from "vuex";
const { mapState, mapMutations } = createNamespacedHelpers("bookModule");
export default {
  data() {
    return {
      addBookData: []
    };
  },
  created() {
    this.addBookData = this.addBookId;
    
  },
  computed: {
    ...mapState(["addBookId"])
  },
  methods: {
    //返回
    onClickLeft() {
      this.$router.go(-1);
    },
    del(index) {
      this.addBookData.splice(index, 1);
      this.$toast("移出书架成功！");
    },
    bookDetail(item) {
      let _id = item._id;
      this.$router.push({
        name: "Detail",
        query: { id: _id }
      });
    }
  }
};
</script>

<style lang="less" scoped>
.bookshelf {
  .bookshelf-item {
    box-sizing: border-box;
    border-bottom: 1px solid #ccc;
    padding: 10px;
    position: relative;
    .bookshelf-img {
      width: 74px;
      height: 92px;
      img {
        width: 74px;
