        font-size: 16px;
      }
      .bookshelf-author {
        color: #969ba3;
        font-size: 13px;
        margin: 6px 0;
      }
      .bookshelf-shortIntro {
        margin-bottom: 6px;
        color: #969ba3;
        font-size: 14px;
      }
      .bookshelf-lastChapter {
        color: #4c4c4c;
        font-size: 13px;
      }
    }
    .delete {
      position: absolute;
      right: 10px;
      top: 10px;
      z-index: 10000;
    }
  }
  .bookshelf-item:nth-last-child(1) {
    border: none;
  }
}
</style>